.loading {
  height: 70vh;
}
.loading-spinner {
  border: 4px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  border-top: 4px solid var(--color-primary);
  width: 24px;
  height: 24px;
  animation: spin 1s linear infinite;
  margin: 0 auto;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.prodPag {
  display: flex;
  flex-direction: column;
}
.userpages {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.sideNavs {
  display: flex;
  flex-direction: column;
  width: 5%;
  align-content: flex-start;
  justify-content: center;
  background-color: var(--color-tertiary-tint3);
  height: 100vh;
  /* height: 100%; */
}

.emptyPage {
  font-size: larger;
  width: 50%;
  margin: auto;
  /* position: absolute; */
  margin-top: -70vh;
}

.searchField {
  width: 50%;
  padding: 10px;
  border: 2px solid var(--color-tertiary-tint3);
  border-radius: 20px;
  /* border-color: var(--color-tertiary-tint1); */
  align-self: center;
}

.listProducts {
  width: 80%;
  background-color: var(--color-white);
  display: flex;
  justify-content: space-between;
  align-content: center;
  flex-wrap: wrap;
  justify-content: center;
  list-style-type: none;
  margin: auto;
  color: var(--color-tertiary);
}

.listStores {
  width: 80%;
  background-color: var(--color-white);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-right: 8%;
  color: var(--color-tertiary);
}

.addToCart {
  display: flex;
  background-color: var(--color-primary);
  border-radius: 5px;
  color: var(--color-white);
  align-content: center;
  justify-content: space-between;
  padding: 2px 25px;
}
.profileproduct {
  width: 70%;
  background-color: var(--color-white);
  display: flex;
  justify-content: space-between;
  /* padding: 20px; */
  align-content: center;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  list-style-type: none;
}

.eachStore {
  display: flex;
  width: 100%;
  justify-content: space-around;
  align-content: space-around;
  background-color: var(--color-primary);
  border-radius: 10px;
  margin: 10px 0;
  color: var(--color-white);
}

.storeDistance {
    display:flex;
    justify-content: end;
    align-content: end;
}

.storeData {
  align-content: start;
  justify-content: start;
}

.eachproduct {
  width: 290;
  height: 370;
  border-radius: 10px;
  margin: 10px;
  display: flex;
  flex-direction: column;
  padding: 5px;
  align-content: center;
  box-shadow: 1px 2px var(--color-tertiary-tint3);
  background-color: var(--color-white);
  border: 1px solid var(--color-tertiary-tint3);
  color: var(--color-tertiary);
}

.avail {
  font-size: 8px;
  color: var(--color-tertiary);
  align-self: start;
}

.scale {
  font-size: smaller;
  align-self: flex-start;
}

.productImage {
  width: 150px;
  height: 120px;
  object-fit: cover;
  align-self: center;
  margin: auto;
}

.storeImage {
    width: 100px;
    height: 100px;
    object-fit: cover;
    align-self: center;
    margin: auto;
  }

.farmname {
  font-size: 10px;
  align-self: flex-start;
}

.productname {
  font-size: medium;
  font-weight: bold;
  align-self: flex-start;
}

.pagination {
  margin-bottom: 100px;
}

.mfooter {
  display: none;
}

.footer {
  display: flex;
}

@media (max-width: 1100px) {
  .profilemenu {
    display: none;
    visibility: hidden;
  }

  .profileproduct {
    width: 100%;
  }

  .profimg {
    width: 5px;
    height: 5px;
  }

  .eachproduct {
    width: 42%;
    height: fit-content;
    border-radius: 10px;
    margin: 10px;
    display: flex;
    flex-direction: column;
    padding: 5px;
    align-content: center;
    box-shadow: 1px 2px var(--color-tertiary-tint2);
    max-width: 150px;
    background-color: var(--color-white);
  }

  .productImage {
    width: 80%;
    object-fit: cover;
    align-self: center;
    margin: auto;
  }

  .searchField {
    width: 80%;
  }
  .listProducts {
    width: 95%;
  }
  .addToCart {
    padding: 2px 10px;
  }
  .sideNavs {
    visibility: hidden;
  }
  .mfooter {
    display: flex;
    position: fixed;
    bottom: 0;
    margin: auto;
    width: 100%;
    background-color: var(--color-white);
    padding: 10px;
    box-shadow: 0 -2px 5px rgba(0, 0, 0, 0.2);
    justify-content: center;
  }
  .footer {
    display: none;
  }
}
