/* For mobile view */
@media (max-width: 1100px) {
  .navcontainer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* padding: 0 5%; */
  }

  .comp_logo {
    height: 100px; /* Adjust the height as needed for mobile view */
    margin-right: auto; /* Push the logo to the right */
  }

  .nav-buttonContainer {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }

  .lbutton,
  .nbutton {
    padding: 5px;
    color: var(--color-white);
    background-color: var(--color-secondary);
    text-decoration: none;
    font-weight: bold;
    border-radius: 5px;
    font-size: 10px;
    margin: 4px 0; /* Adjust margin as needed for spacing between buttons */
  }

  /* Hide "About Us" and "Contact Us" buttons on mobile */
  .nbutton {
    display: none;
  }

  .sideImage {
    width: 80px;
  }

  .topText {
    text-shadow: 3px 3px var(--color-tertiary-tint2);
    font-size: 36px;
  }

  .appContainer {
    width: 80%;
  }

  .eachIcon {
    padding: 3px 13px;
    font-size: 12px;
    font-weight: bold;
    margin: 20px auto;
  }
  .schedule {
    /* padding: 10px; */
    margin: 2% 5%;
  }

  .schedule1 {
    margin: 5px;
    padding-left: 0px;
    border-radius: 10px;
  }

  .schedule1 > h4 {
    margin-top: 270px;
    font-size: larger;
    width: 90%;
    padding: 5px 10px;
  }

  .schedule1 > p {
    font-size: small;
    padding: 5px 10px;
  }
  .schedule2,
  .schedule3 {
    margin: 5px;
    padding-left: 0px;
    border-radius: 10px;
  }

  .schedule2 > h4,
  .schedule3 > h4 {
    margin-top: 70px;
    font-size: larger;
    width: 90%;
    padding: 5px 10px;
  }

  .schedule2 > p,
  .schedule3 > p {
    font-size: small;
    padding: 5px 10px;
  }

  .reglogistics > .regstore1 {
    padding-left: 30px;
  }

  .money {
    width: 40%;
    height: 40%;
    align-self: center;
    margin: o;
  }
  .regstore1 {
    margin: 5% 10% 5% 0;
    width: 60%;
    padding: 0;
    /* margin: 0; */
    /* justify-content: space-around; */
  }
  .regstore1 > h1 {
    font-weight: bold;
    font-size: 15px;
    width: 40%;
  }
  .regstore1 > p {
    font-weight: normal;
    font-size: 12px;
  }

  .cbutton {
    padding: 3px 5px;
    border-radius: 5px;
    color: var(--color-white);
    font-weight: bold;
    font-size: 10px;
  }
  .footer {
    display: flex;
    flex-direction: column;
  }
}

