.foot {
    display: flex;
    justify-content: space-between;
    align-items: center;
    align-self: center;
    background-color: #fff;
  }
  
  .eachFoot {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 0px 9px;
    margin: auto;
  }
  
  .eachFoot p {
    margin-top: 2px;
    font-size: smaller;
  }

.eachFoot.selected {
    color: green;
}
